import React, { useRef, useEffect, useState } from "react";
import mapboxgl from "mapbox-gl";
import MapboxDirections from "@mapbox/mapbox-gl-directions/dist/mapbox-gl-directions";

import "./styles.css";
mapboxgl.accessToken =
  "pk.eyJ1IjoibWFydGlucmZoIiwiYSI6ImNraGRsOWZraDA2dmwyc24wbmllNjR0NGsifQ.yknV55AANfyEcVLAqKHuYw";

export default function Map() {
  const mapContainerRef = useRef(null);

  const [lng, setLng] = useState(-2.24);
  const [lat, setLat] = useState(53.48);
  const [zoom, setZoom] = useState(1.5);

  // Initialize map when component mounts
  useEffect(() => {
    const map = new mapboxgl.Map({
      container: mapContainerRef.current,
      style: "mapbox://styles/martinrfh/ckhgi8e6v1hkc19qcfcn8a7hh",
      center: [lng, lat],
      zoom: zoom,
    });

    // Add navigation control (the +/- zoom buttons)
    map.addControl(new mapboxgl.NavigationControl(), "top-right");

    map.addControl(
      new MapboxDirections({
        accessToken:
          "pk.eyJ1IjoibWFydGlucmZoIiwiYSI6ImNraGRsOWZraDA2dmwyc24wbmllNjR0NGsifQ.yknV55AANfyEcVLAqKHuYw",
      }),
      "top-left"
    );

    map.addControl(
      new mapboxgl.GeolocateControl({
        positionOptions: {
          enableHighAccuracy: true,
        },
        trackUserLocation: true,
      })
    );

    map.on("move", () => {
      setLng(map.getCenter().lng.toFixed(4));
      setLat(map.getCenter().lat.toFixed(4));
      setZoom(map.getZoom().toFixed(2));
    });

    // Clean up on unmount
    return () => map.remove();
  }, []); // eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div>
      <div className="sidebarStyle">
        <div>
          Longitude: {lng} | Latitude: {lat} | Zoom: {zoom}
        </div>
      </div>
      <div className="map-container" ref={mapContainerRef} />
    </div>
  );
}
